@media (max-width: 600px) {
  .header-content {
    display: flex;
    flex-direction: column;
    padding: 20px;
  }

  .header-image-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .header-image {
    max-width: 80vw;
  }

  .name {
    position: absolute;
    bottom: 0;
    text-align: center;
    text-wrap: pretty;
  }

  .main-content {
    margin: 20px;
  }

  .about-me,
  .my-work,
  .contact-me {
    text-align: center;
  }

  .link-logos {
    display: flex;
    justify-content: center;
  }

  .card-container {
    grid-template-columns: 1fr;
    grid-gap: 30px;
  }

  .footer-content {
    flex-direction: column;
    margin: 0px;
  }

  .contact-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }

  .footer-image {
    width: 100vw;
    justify-self: center;
  }
}

@media (min-width: 601px) and (max-width: 1200px) {
  .header-content,
  .main-content,
  .footer-content {
    padding: 40px;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .header-content {
    display: inline-block;
  }

  .header-image-container {
    z-index: 1;
    float: left;
    margin-right: 15px;
    margin-bottom: 10px;
  }

  .name {
    position: absolute;
    right: 0;
    margin-right: 50px;
  }

  .header-text-container {
    display: block;
    margin-top: 150px;
    margin-left: 100px;
    padding: 20px;
    background-color: #fff;
    border: 1px solid lightgrey;
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.288);
  }

  .about-me {
    text-align: end;
    padding-right: 20px;
  }

  .link-logos {
    justify-content: end;
    padding-right: 20px;
  }

  .main-content {
    padding-top: 0px;
  }

  .header-image {
    width: 40vw;
  }

  .footer-image {
    max-width: 60vw;
  }

  .card-container {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
  }

  .contact-content {
    padding-right: 30px;
  }
}

