body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

.slanted {
  background: #018ab4;
  width: 100%;
  height: 300px;
  transform: skewY(-8deg) scaleY(5) translateY(-40%);
  position: absolute;
  top: 0;
  z-index: -1;
  box-shadow: 1px 1px 4px 0.3px lightgrey;
}

.header-image-container {
  height: 100%;
}

.header-image {
  display: flex;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.288);
  object-fit: fill;
}

.name,
.contact-me {
  font-size: 48px;
  color: rgb(247, 242, 236);
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.308);
}

.header-text-container {
  flex-direction: column;
  height: fit-content;
  font-size: 20px;
}

.about-me,
.my-work {
  font-size: 36px;
}

.link-logos {
  display: flex;
  gap: 15px;
  padding-top: 10px;
}

.logo {
  width: 35px;
}

.card-container {
  display: grid;
  background-color: #fff;
  color: #444;
}

.card {
  max-width: 200px;
  border: 1px solid lightgrey;
  box-shadow: 1px 1px 4px 1px lightgrey;
  padding: 20px;
  justify-self: center;
}

.card-image {
  max-width: 200px;
}

.card-title {
  display: flex;
  justify-content: space-between;
  padding-top: 5px;
}

.project-title {
  margin: 0;
}

.card-logos {
  display: flex;
  align-items: center;
  gap: 10px;
}

.card-logo {
  width: 25px;
}

footer {
  background: #018ab4;
}

.footer-content {
  display: flex;
  justify-content: space-between;
}

.contact-me-text {
  color: rgb(247, 242, 236);
  font-size: 20px;
}

.contact-content {
  min-width: fit-content;
}

.link-logos-contact {
  display: flex;
  gap: 15px;
  padding-top: 20px;
}

.footer-image {
  display: flex;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.288);
}
